import { ApiService } from './../../../services/api.service';
import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';
import * as Quill from 'quill'

@Component({
    selector: 'app-responder-atividade',
    templateUrl: './responder-atividade.page.html',
    styleUrls: ['./responder-atividade.page.scss'],
})
export class ResponderAtividadePage implements OnInit {
    slideAtual = 1

    atividade = null
    editorView
    editor

    listaPontos = []
    pontoFocal = ''
    constructor(
        public loadingCtrl: LoadingController,
        public route: ActivatedRoute,
        private api: ApiService,
    ) {
        // let id_atividade = this.route.snapshot.paramMap.get('id');
    }
    ionViewDidEnter() {
        let id_atividade = this.route.snapshot.paramMap.get('id');
        this.abrirAtividade(id_atividade)
    }

    ngOnInit() {
        let toolbarOptions = [
            [{ 'font': [] }],
            ['bold', 'italic', 'underline', 'strike'],        // toggled buttons

            [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme

            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            [{ 'script': 'sub' }, { 'script': 'super' }],      // superscript/subscript
            [{ 'indent': '-1' }, { 'indent': '+1' }],          // outdent/indent

            [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],

            [{ 'align': [] }],

            ['clean']
        ]
        this.editorView = new Quill('#editorView', {
            theme: 'snow', modules: {
                toolbar: toolbarOptions
            }
        })
        this.editor = new Quill('#editorSintese', {
            theme: 'snow', modules: {
                toolbar: toolbarOptions
            }
        })
    }

    async abrirAtividade(id) {
        let loading = await this.loadingCtrl.create({ message: "Buscando atividades..." })
        loading.present()
        let body = `
            query {
                retornarAtividade(id_atividade: ${id}){
                    id
                    titulo
                    autor
                    texto
                    imagens{
                        imagem{
                            link
                        }
                    }
                }
            }
        `
        let retorno = await this.api.get(body)
        loading.dismiss()
        if (!retorno.errors && retorno.data.retornarAtividade) {
            this.atividade = retorno.data.retornarAtividade
            this.processarDados()
        } else {

        }
    }

    processarDados() {
        console.log("Texto: ", (JSON.parse(this.atividade.texto)));

        this.editorView.setContents(JSON.parse(this.atividade.texto))
        let justHtmlContent = document.getElementById('textoView');
        var justHtml = this.editorView.root.innerHTML;
        justHtmlContent.innerHTML = justHtml;
    }

    avancar() {
        this.slideAtual++;
    }

    voltar() {
        this.slideAtual--;
    }

    adicionarPonto() {
        this.listaPontos.push(JSON.parse(JSON.stringify(this.pontoFocal)))
        this.pontoFocal= ''
    }
    concluir(){
        
    }

}
